'use client'

import { ListItem, ListItemText, Stack, Typography, type ListItemProps } from '@mui/material';
import { type ProjectData } from '@strife/common';
import { JSX } from 'react';

export interface ProjectListItemProps extends ListItemProps {
  projectData: ProjectData
}

export default function ProjectListItem({ projectData, ...listItemProps }: ProjectListItemProps): JSX.Element {
  return <ListItem {...listItemProps}>
    <ListItemText primary={projectData.name} secondary={projectData.description} />
  </ListItem>;
}
