'use client'

import { Typography, type TypographyProps } from '@mui/material';
import { JSX } from 'react';

export interface LogoProps extends TypographyProps {}

export default function Logo({ ...typographyProps }: LogoProps): JSX.Element {
  return <Typography variant="h2" align="center" fontWeight={700} {...typographyProps}>strife</Typography>;
}
