'use client'

import di from '@/DependencyInjection';
import AccountAvatar from '@components/account/AccountAvatar';
import Logo from '@components/Logo';
import { UserServiceId } from '@interfaces/IUser';
import { AuthenticationServiceServiceId } from '@interfaces/services/IAuthenticationService';
import { Menu as MenuIcon } from '@mui/icons-material';
import { AppBar, Box, Button, Divider, IconButton, Menu, MenuItem, Toolbar, type AppBarProps } from '@mui/material';
import { useRouter } from 'next/navigation';
import { Fragment, JSX, useRef, useState } from 'react';

export interface NavBarProps extends AppBarProps {}

export default function NavBar({ ...appBarProps }: NavBarProps): JSX.Element {
  const user = di.get(UserServiceId);
  const authenticationService = di.get(AuthenticationServiceServiceId);
  const router = useRouter();
  const [ isNavigationMenuOpen, setIsNavigationMenuOpen ] = useState(false);
  const navigationMenuAchorRef = useRef<HTMLButtonElement | null>(null);
  const [ isUserMenuOpen, setIsUserMenuOpen ] = useState(false);
  const userMenuAchorRef = useRef<HTMLButtonElement | null>(null);

  return <Box sx={{ flexGrow: 1 }}>
    <AppBar position='static' {...appBarProps}>
      <Toolbar>
        <IconButton ref={navigationMenuAchorRef} size='large' edge='start' color='inherit' aria-label='menu' sx={{ mr: 2 }} onClick={onClickOpenNavigationMenu}>
          <MenuIcon />
        </IconButton>
        <Menu anchorEl={navigationMenuAchorRef.current} open={isNavigationMenuOpen} onClose={onCloseNavigationMenu}>
          <MenuItem href='/projects'>Projects</MenuItem>
        </Menu>
        <Logo align='left' variant='h6' sx={{ flexGrow: 1 }}/>
        {
          user && user.account
            ? <Fragment>
                <IconButton ref={userMenuAchorRef} onClick={onClickOpenUserMenu}>
                  <AccountAvatar accountData={user.account} />
                </IconButton>
                <Menu anchorEl={userMenuAchorRef.current} open={isUserMenuOpen} onClose={onCloseUserMenu}>
                  <MenuItem onClick={onClickLogout}>Logout</MenuItem>
                  <Divider />
                  <MenuItem onClick={onClickRefreshAccessToken}>Refresh Access Token</MenuItem>
                  <MenuItem onClick={onClickClearLocalStorage}>Clear Local Storage</MenuItem>
                </Menu>
              </Fragment>
            : <Button color='inherit' href='/login'>Login</Button>
        }
      </Toolbar>
    </AppBar>
  </Box>;

  function onClickOpenNavigationMenu(): void {
    setIsNavigationMenuOpen(true);
  }

  function onCloseNavigationMenu(): void {
    setIsNavigationMenuOpen(false);
  }

  function onClickOpenUserMenu(): void {
    setIsUserMenuOpen(true);
  }

  function onCloseUserMenu(): void {
    setIsUserMenuOpen(false);
  }

  async function onClickLogout(): Promise<void> {
    try {
      await authenticationService.logout();
    } finally {
      router.refresh();
    }
  }

  async function onClickRefreshAccessToken(): Promise<void> {
    const accountData = await authenticationService.refresh();
  }

  function onClickClearLocalStorage(): void {
    localStorage.clear();
  }
}
